import React from 'react'
import {Card, Cascader, Tooltip, Icon, Form, Checkbox, Select, Input, Button, Col, Row, message, BackTop} from 'antd'
import CustomBreadcrumb from '../../../components/CustomBreadcrumb/index'
import TypingCard from '../../../components/TypingCard'

const FormItem = Form.Item
const Option = Select.Option

const options = [
  {
    label: '',
    value: 'hubei',
    children: [
      {
        label: '',
        value: 'wuhang',
        children: [
          {
            label: '',
            value: 'caidian'
          },
          {
            label: '',
            value: 'jiangxia'
          }
        ]
      },
      {
        label: '',
        value: 'yichang',
        children: [
          {
            label: '',
            value: 'wujiagang'
          },
          {
            label: '',
            value: 'yilingqu'
          },
          {
            label: '',
            value: 'jiangnan'
          },
          {
            label: '',
            value: 'kaifaqu'
          },
          {
            label: 'CBD',
            value: 'CBD'
          }
        ]
      }
    ]
  }
]

@Form.create()
class FormDemo1 extends React.Component {
  state = {
    text: '',
    disabled: false,

  }
  timer = 0
  countdown = (e) => {
    let time = 60
    this.setState({
      text: --time + 's',
      disabled: true
    })
    this.timer = setInterval(() => {
      if (time > 0) {
        this.setState({
          text: --time + 's',
          disabled: true
        })
      } else {
        this.setState({
          text: '',
          disabled: false
        })
      }
    }, 1000)
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (err) {
        message.warning('')
      } else {
        message.success('')
        // console.log(values)
      }
    });
  }

  componentWillUnmount() {
    clearInterval(this.timer)
  }

  render() {
    const {getFieldDecorator, getFieldValue} = this.props.form
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 4},
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 12},
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 12,
          offset: 4,
        },
      },
    }
    const prefixSelector = getFieldDecorator('prefix', {
      initialValue: 86,
    })(
      <Select style={{width: 70}}>
        <Option value={86}>+86</Option>
        <Option value={87}>+87</Option>
      </Select>
    );
    const cardContent = ''
    return (
      <div>
        <CustomBreadcrumb arr={['', '', '']}/>
        <TypingCard source={cardContent}/>
        <Card bordered={false} title=''>
          <Form layout='horizontal' style={{width: '70%', margin: '0 auto'}} onSubmit={this.handleSubmit}>
            <FormItem label='' {...formItemLayout}>
              {
                getFieldDecorator('email', {
                  rules: [
                    {
                      type: 'email',
                      message: ''
                    },
                    {
                      required: true,
                      message: ''
                    }
                  ]
                })(
                  <Input/>
                )
              }
            </FormItem>
            <FormItem label='' {...formItemLayout}>
              {
                getFieldDecorator('password', {
                  rules: [
                    {
                      required: true,
                      message: ''
                    },
                    {
                      min: 6,
                      message: '6'
                    },
                    {
                      max: 16,
                      message: '16'
                    },
                    {
                      whitespace: true,
                      message: ''
                    }
                  ]
                })(
                  <Input type='password'/>
                )
              }
            </FormItem>
            <FormItem label='' {...formItemLayout} required>
              {
                getFieldDecorator('confirm', {
                  rules: [
                    {
                      validator: (rule, value, callback) => {
                        const {getFieldValue} = this.props.form
                        if (!getFieldValue('password')) {
                          callback('')
                        }
                        if (value && value !== getFieldValue('password')) {
                          callback('')
                        }
                        callback()
                      }
                    }
                  ]
                })(
                  <Input type='password'/>
                )
              }
            </FormItem>
            <FormItem {...formItemLayout} label={(
              <span>
                &nbsp;
                <Tooltip title=''>
                  <Icon type='question-circle-o'/>
                </Tooltip>
              </span>
            )}>
              {
                getFieldDecorator('nickname', {
                  rules: []
                })(
                  <Input/>
                )
              }
            </FormItem>
            <FormItem label='' {...formItemLayout} required>
              {
                getFieldDecorator('residence', {
                  rules: [
                    {
                      type: 'array',
                      required: true,
                      message: ''
                    }
                  ]
                })(
                  <Cascader options={options} expandTrigger="hover" placeholder=''/>
                )
              }
            </FormItem>
            <FormItem label='' {...formItemLayout}>
              {
                getFieldDecorator('phone', {
                  rules: [
                    {
                      len: 11,
                      pattern: /^[1][3,4,5,7,8][0-9]{9}$/,
                      required: true,
                      message: '11'
                    }
                  ]
                })(
                  <Input addonBefore={prefixSelector}/>
                )
              }
            </FormItem>
            <FormItem label='' {...formItemLayout}>
              {
                getFieldDecorator('website', {
                  rules: [
                    {
                      pattern: /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+)\.)+([A-Za-z0-9-~/])+$/,
                      message: ''
                    }
                  ]
                })(
                  <Input/>
                )
              }
            </FormItem>
            <FormItem{...formItemLayout} label="">
              <Row gutter={8}>
                <Col span={12}>
                  {getFieldDecorator('captcha', {
                    rules: [{required: true, message: ''}],
                  })(
                    <Input/>
                  )}
                </Col>
                <Col span={12}>
                  <Button disabled={this.state.disabled} onClick={(e) => this.countdown(e)}>{this.state.text}</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem {...tailFormItemLayout}>
              {getFieldDecorator('agreement', {
                valuePropName: 'checked',
              })(
                <Checkbox><a></a></Checkbox>
              )}
            </FormItem>
            <FormItem style={{textAlign: 'center'}} {...tailFormItemLayout}>
              <Button type="primary" htmlType="submit" disabled={!getFieldValue('agreement')}></Button>
            </FormItem>
          </Form>
        </Card>
        <BackTop visibilityHeight={200} style={{right: 50}}/>
      </div>
    )
  }
}

export default FormDemo1


